// UIModule.ts

// import { utils } from '@milkdown/kit';
import { defaultValueCtx, Editor, rootCtx } from '@milkdown/kit/core';
import { commonmark } from '@milkdown/kit/preset/commonmark';
import { gfm } from '@milkdown/kit/preset/gfm';
import { nord } from '@milkdown/theme-nord';
import { listener, listenerCtx } from '@milkdown/plugin-listener';
import {
  criticAddition,
  criticDeletion,
  criticHighlight,
  criticComment,
  criticSubstitution,
  criticKeymap,
  criticMarkupPlugin,
  criticMarkupRemarkPlugin,
} from './milkdown-criticmarkup';
import type { ChangesModule } from '@modules/changes';
import type { MarkdownModule } from '@modules/markdown';
import type { CommentsModule } from '@modules/comments';
import type { Element as ReviewElement } from '@/types';
import { MilkdownPlugin } from '@milkdown/kit/ctx';

export interface UIConfig {
  changes: ChangesModule;
  markdown: MarkdownModule;
  comments: CommentsModule;
  inlineEditing?: boolean; // Use inline editing instead of modal
}

export class UIModule {
  private config: UIConfig;
  private activeEditor: HTMLElement | null = null;
  private currentElementId: string | null = null;
  private milkdownEditor: Editor | null = null;
  private currentEditorContent: string = '';
  private showTrackedChanges: boolean = true;

  constructor(config: UIConfig) {
    this.config = config;
    console.log(
      '[QuartoReview] UIModule initialized with tracked changes:',
      this.showTrackedChanges
    );
  }

  /**
   * Toggle visibility of tracked changes
   */
  public toggleTrackedChanges(): void {
    this.showTrackedChanges = !this.showTrackedChanges;
    this.refresh();
  }

  /**
   * Get current tracked changes mode
   */
  public isShowingTrackedChanges(): boolean {
    return this.showTrackedChanges;
  }

  public attachEventListeners(): void {
    // Select both .review-editable and any element with data-review-id (for headers/sections)
    const editableElements = document.querySelectorAll('[data-review-id]');
    editableElements.forEach((elem) => {
      elem.addEventListener('click', (e) => {
        // Don't open editor if already editing or if click is on inline editor
        if (
          elem.classList.contains('review-editable-editing') ||
          (e.target as HTMLElement).closest('.review-inline-editor-container')
        ) {
          return;
        }
        e.stopPropagation();
        const id = elem.getAttribute('data-review-id');
        if (id) {
          this.openEditor(id);
        }
      });
      elem.addEventListener('mouseenter', () => {
        if (!elem.classList.contains('review-editable-editing')) {
          elem.classList.add('review-hover');
        }
      });
      elem.addEventListener('mouseleave', () => {
        elem.classList.remove('review-hover');
      });

      // Add text selection handler for comments
      elem.addEventListener('mouseup', (e) => {
        this.handleTextSelection(e, elem);
      });
    });

    document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape' && this.activeEditor) {
        this.closeEditor();
      }
    });

    // Close comment popup when clicking outside
    document.addEventListener('mousedown', (e) => {
      const popup = document.querySelector('.review-comment-popup');
      if (popup && !popup.contains(e.target as Node)) {
        popup.remove();
      }
    });
  }

  public openEditor(elementId: string): void {
    const element = document.querySelector(
      `[data-review-id="${elementId}"]`
    ) as HTMLElement | null;
    if (!element) return;

    const targetElement =
      this.resolveListEditorTarget(element) ?? element;
    const targetId = targetElement.getAttribute('data-review-id');
    if (!targetId) return;

    if (targetId !== elementId) {
      console.log(
        `[QuartoReview] Redirecting edit to list root ${targetId} (clicked ${elementId})`
      );
    }

    // Check if inline editing is enabled
    if (this.config.inlineEditing) {
      this.openInlineEditor(targetId);
    } else {
      this.openModalEditor(targetId);
    }
  }

  private openModalEditor(elementId: string): void {
    const element = document.querySelector(`[data-review-id="${elementId}"]`);
    if (!element) return;

    this.currentElementId = elementId;
    const rawContent = this.config.changes.getElementContent(elementId);
    console.log('[QuartoReview] Opening editor for', elementId);
    console.log(
      '[QuartoReview] Tracked changes enabled:',
      this.showTrackedChanges
    );
    console.log('[QuartoReview] Editor content (raw):', rawContent);

    // Clean content: remove any nested review-editable fence divs
    // This fixes legacy documents that have nested divs in their markdown
    const editorContent = this.cleanNestedDivs(rawContent);

    const type = element.getAttribute('data-review-type') || 'Para';

    const modal = this.createEditorModal(editorContent, type);
    document.body.appendChild(modal);
    this.activeEditor = modal;

    // Delay so DOM renders
    requestAnimationFrame(() => {
      this.initializeMilkdown(modal, editorContent);
    });
  }

  private openInlineEditor(elementId: string): void {
    const element = document.querySelector(
      `[data-review-id="${elementId}"]`
    ) as HTMLElement;
    if (!element) return;

    // Close any existing inline editor
    this.closeEditor();

    this.currentElementId = elementId;
    const rawContent = this.config.changes.getElementContent(elementId);
    const editorContent = this.cleanNestedDivs(rawContent);

    const type = element.getAttribute('data-review-type') || 'Para';

    // Mark element as being edited
    element.classList.add('review-editable-editing');

    // Wrap original content
    const originalContent = element.innerHTML;
    element.innerHTML = `
      <div class="review-editable-original-content">${originalContent}</div>
      <div class="review-inline-editor-container">
        <div class="review-inline-editor-toolbar">
          <span class="review-inline-editor-label">✏️ ${type}</span>
          <div class="review-inline-editor-actions">
            <button class="review-btn review-btn-secondary review-btn-sm" data-action="cancel">Cancel</button>
            <button class="review-btn review-btn-primary review-btn-sm" data-action="save">Save</button>
          </div>
        </div>
        <div class="review-inline-editor-body"></div>
      </div>
    `;

    const inlineEditor = element.querySelector(
      '.review-inline-editor-container'
    ) as HTMLElement;
    this.activeEditor = inlineEditor;

    // Add event listeners
    inlineEditor.querySelectorAll('[data-action="cancel"]').forEach((btn) => {
      btn.addEventListener('click', () => this.closeEditor());
    });

    inlineEditor
      .querySelector('[data-action="save"]')
      ?.addEventListener('click', () => {
        this.saveEditor();
      });

    // Initialize Milkdown
    requestAnimationFrame(() => {
      this.initializeMilkdown(inlineEditor, editorContent);
    });
  }

  private async initializeMilkdown(
    container: HTMLElement,
    content: string
  ): Promise<void> {
    // Find the editor body - works for both modal and inline
    const editorContainer =
      container.querySelector('.review-editor-body') ||
      container.querySelector('.review-inline-editor-body');
    if (!editorContainer) return;

    try {
      // Initialize current content
      const initialContent = this.normalizeListMarkers(content);
      this.currentEditorContent = initialContent;

      // Create Milkdown editor
      this.milkdownEditor = await Editor.make()
        .config((ctx) => {
          ctx.set(rootCtx, editorContainer);
          ctx.set(defaultValueCtx, initialContent);

          // Listen for markdown changes
          ctx.get(listenerCtx).markdownUpdated((_ctx, markdown) => {
            const normalized = this.normalizeListMarkers(markdown);
            this.currentEditorContent = normalized;
            console.log('[QuartoReview] Markdown updated:', normalized);
          });
        })
        .config(nord)
        .use(commonmark)
        .use(gfm)
        .use(listener)
        // CriticMarkup support
        .use(criticMarkupRemarkPlugin)
        .use(criticAddition)
        .use(criticDeletion)
        .use(criticHighlight)
        .use(criticComment)
        .use(criticSubstitution)
        .use(criticKeymap)
        .use(criticMarkupPlugin as unknown as MilkdownPlugin[])
        .create();

      console.log('[QuartoReview] Milkdown editor initialized successfully');
    } catch (error) {
      console.error('[QuartoReview] Failed to initialize Milkdown:', error);
      editorContainer.innerHTML = `
        <div style="padding:20px; color:red;">
          Failed to initialize editor. Please try again.
          <pre>${error instanceof Error ? error.message : String(error)}</pre>
        </div>
      `;
    }
  }

  public closeEditor(): void {
    if (this.milkdownEditor) {
      this.milkdownEditor.destroy();
      this.milkdownEditor = null;
    }
    if (this.activeEditor) {
      // For inline editing, restore the element
      if (this.config.inlineEditing && this.currentElementId) {
        const element = document.querySelector(
          `[data-review-id="${this.currentElementId}"]`
        ) as HTMLElement;
        if (element) {
          element.classList.remove('review-editable-editing');
          const originalContent = element.querySelector(
            '.review-editable-original-content'
          );
          if (originalContent) {
            element.innerHTML = originalContent.innerHTML;
          }
        }
      } else {
        // For modal editing, just remove the modal
        this.activeEditor.remove();
      }
      this.activeEditor = null;
      this.currentElementId = null;
      this.currentEditorContent = '';
    }
  }

  private saveEditor(): void {
    if (!this.milkdownEditor || !this.currentElementId) return;

    // Get markdown content from tracked state
    let newContent = this.normalizeListMarkers(this.currentEditorContent);
    this.currentEditorContent = newContent;

    // Get original content for comparison
    const originalContent = this.normalizeListMarkers(
      this.config.changes.getElementContent(this.currentElementId)
    );
    console.log('[QuartoReview] Saving editor');
    console.log('[QuartoReview] Original (normalized):', originalContent);
    console.log('[QuartoReview] New (normalized):', newContent);

    const normalizeForComparison = (str: string) =>
      this.normalizeListMarkers(str)
        .replace(/\r\n/g, '\n') // Normalize line endings
        .replace(/[ \t]+$/gm, '') // Remove trailing spaces
        .replace(/\n{3,}/g, '\n\n') // Collapse multiple newlines
        .trim();

    const originalNormalized = normalizeForComparison(originalContent);
    const newNormalized = normalizeForComparison(newContent);

    // Only save if content actually changed
    if (originalNormalized === newNormalized) {
      console.log(
        '[QuartoReview] No meaningful changes detected, skipping save'
      );
      this.closeEditor();
      return;
    }

    this.config.changes.edit(this.currentElementId, newContent);
    this.refresh();
    this.closeEditor();
  }

  private createEditorModal(_content: string, type: string): HTMLElement {
    const modal = document.createElement('div');
    modal.className = 'review-editor-modal';
    modal.innerHTML = `
      <div class="review-editor-container">
        <div class="review-editor-header">
          <h3>Edit ${type}</h3>
          <button class="review-btn review-btn-secondary" data-action="close">✕</button>
        </div>
        <div class="review-editor-body"></div>
        <div class="review-editor-footer">
          <button class="review-btn review-btn-secondary" data-action="cancel">Cancel</button>
          <button class="review-btn review-btn-primary" data-action="save">Save</button>
        </div>
      </div>
    `;

    modal
      .querySelector('[data-action="close"]')
      ?.addEventListener('click', () => this.closeEditor());
    modal
      .querySelector('[data-action="cancel"]')
      ?.addEventListener('click', () => this.closeEditor());
    modal
      .querySelector('[data-action="save"]')
      ?.addEventListener('click', () => this.saveEditor());

    modal.addEventListener('click', (e) => {
      if (e.target === modal) {
        this.closeEditor();
      }
    });

    return modal;
  }

  public refresh(): void {
    const currentState = this.config.changes.getCurrentState();
    const operations = this.config.changes.getOperations();

    currentState.forEach((elem) => {
      // If showing tracked changes, get content with CriticMarkup visualization
      if (this.showTrackedChanges) {
        const hasEdits = operations.some(
          (op) => op.elementId === elem.id && op.type === 'edit'
        );

        if (hasEdits) {
          // Get content with tracked changes visualization
          const contentWithChanges =
            this.config.changes.getElementContentWithTrackedChanges(elem.id);

          // Create a modified element for display
          const displayElem = {
            ...elem,
            content: contentWithChanges,
          };
          this.updateElementDisplay(displayElem);
        } else {
          this.updateElementDisplay(elem);
        }
      } else {
        this.updateElementDisplay(elem);
      }
    });

    const modifiedIds = new Set(
      operations.filter((op) => op.type === 'edit').map((op) => op.elementId)
    );
    modifiedIds.forEach((id) => {
      const element = document.querySelector(`[data-review-id="${id}"]`);
      if (element) {
        element.setAttribute('data-review-modified', 'true');
      }
    });
    this.updateUnsavedIndicator();
  }

  /**
   * Clean nested review-editable div fences from markdown content
   * Removes patterns like: ::: {class="review-editable" ...} content :::
   */
  private cleanNestedDivs(content: string): string {
    // Remove pandoc div fences that represent nested review-editable elements
    // Pattern: ::: {class="review-editable" ...} ... :::
    // Also handles data-review-* attributes in any order
    return content.replace(
      /:::\s*\{[^}]*review-editable[^}]*\}[\s\S]*?:::/g,
      ''
    );
  }

  /**
   * Normalize list markers so Milkdown output (`*`/`+`) becomes canonical `-`.
   * Skips fenced code blocks to avoid touching sample snippets.
   */
  private normalizeListMarkers(content: string): string {
    const lines = content.split(/\r?\n/);
    let fenceDelimiter: string | null = null;

    const normalized = lines.map((line) => {
      const trimmed = line.trim();
      const fenceMatch = trimmed.match(/^(`{3,}|~{3,})/);

      if (fenceMatch) {
        const delimiter = fenceMatch[1];
        if (!fenceDelimiter) {
          fenceDelimiter = delimiter;
        } else if (trimmed.startsWith(fenceDelimiter)) {
          fenceDelimiter = null;
        }
        return line;
      }

      if (fenceDelimiter) {
        return line;
      }

      return line.replace(
        /^(\s*)[*+]\s+/,
        (_match, indent: string) => `${indent}- `
      );
    });

    return normalized.join('\n');
  }

  private updateElementDisplay(elem: ReviewElement): void {
    const domElement = document.querySelector(`[data-review-id="${elem.id}"]`);
    if (!domElement) return;

    // Clean content: remove any nested review-editable fence divs
    let cleanContent = this.cleanNestedDivs(elem.content);

    // For headings, normalize CriticMarkup to single line
    // Headings with newlines inside CriticMarkup break rendering
    if (elem.metadata.type === 'Header') {
      cleanContent = this.normalizeCriticMarkupNewlines(cleanContent);
    }

    console.log('[QuartoReview] Updating display for', elem.id);
    console.log('[QuartoReview] Content:', cleanContent);

    // Render using Remark with CriticMarkup support
    const html = this.config.markdown.renderElement(
      cleanContent,
      elem.metadata.type,
      elem.metadata.level
    );

    console.log('[QuartoReview] Rendered HTML:', html);

    const contentElem = domElement.querySelector(
      ':scope > *:not([data-review-id])'
    );
    if (contentElem) {
      contentElem.innerHTML = html;
    }
  }

  /**
   * Normalize newlines within CriticMarkup for single-line contexts (like headings)
   * Converts: {++ test\n++} → {++ test ++}
   */
  private normalizeCriticMarkupNewlines(content: string): string {
    // Replace newlines within CriticMarkup with spaces
    return content
      .replace(/\{\+\+([\s\S]+?)\+\+\}/g, (_match, text) => {
        return `{++${text.replace(/\s+/g, ' ').trim()}++}`;
      })
      .replace(/\{--([\s\S]+?)--\}/g, (_match, text) => {
        return `{--${text.replace(/\s+/g, ' ').trim()}--}`;
      })
      .replace(/\{~~([\s\S]+?)~>([\s\S]+?)~~\}/g, (_match, old, newText) => {
        return `{~~${old.replace(/\s+/g, ' ').trim()}~>${newText.replace(/\s+/g, ' ').trim()}~~}`;
      })
      .replace(/\{>>([\s\S]+?)<<\}/g, (_match, text) => {
        return `{>>${text.replace(/\s+/g, ' ').trim()}<<}`;
      })
      .replace(/\{==([\s\S]+?)==\}/g, (_match, text) => {
        return `{==${text.replace(/\s+/g, ' ').trim()}==}`;
      });
  }

  /**
   * For list items/sub-lists, resolve the highest ancestor list element so edits apply to the full list.
   */
  private resolveListEditorTarget(
    element: HTMLElement
  ): HTMLElement | null {
    const selector =
      '[data-review-type="BulletList"], [data-review-type="OrderedList"]';
    let current = element.closest(selector) as HTMLElement | null;
    if (!current) {
      return null;
    }

    let highest = current;
    while (current) {
      const parent = current.parentElement?.closest(
        selector
      ) as HTMLElement | null;
      if (!parent) break;
      highest = parent;
      current = parent;
    }

    return highest;
  }

  private updateUnsavedIndicator(): void {
    let indicator = document.querySelector('.review-unsaved-indicator');
    if (this.config.changes.hasUnsavedOperations()) {
      if (!indicator) {
        const toolbar = this.getOrCreateToolbar();
        indicator = document.createElement('div');
        indicator.className = 'review-unsaved-indicator';
        indicator.setAttribute('title', 'Unsaved changes');
        toolbar.appendChild(indicator);
      }
    } else {
      indicator?.remove();
    }
  }

  private getOrCreateToolbar(): HTMLElement {
    let toolbar = document.querySelector('.review-toolbar') as HTMLElement;
    if (!toolbar) {
      toolbar = document.createElement('div');
      toolbar.className = 'review-toolbar';
      const undoBtn = document.createElement('button');
      undoBtn.className = 'review-btn review-btn-secondary';
      undoBtn.textContent = 'Undo';
      undoBtn.addEventListener('click', () => {
        if (this.config.changes.undo()) {
          this.refresh();
        }
      });
      const redoBtn = document.createElement('button');
      redoBtn.className = 'review-btn review-btn-secondary';
      redoBtn.textContent = 'Redo';
      redoBtn.addEventListener('click', () => {
        if (this.config.changes.redo()) {
          this.refresh();
        }
      });
      const commentsBtn = document.createElement('button');
      commentsBtn.className = 'review-btn review-btn-secondary';
      commentsBtn.textContent = '💬 Comments';
      commentsBtn.addEventListener('click', () => {
        this.toggleCommentsSidebar();
      });
      toolbar.appendChild(undoBtn);
      toolbar.appendChild(redoBtn);
      toolbar.appendChild(commentsBtn);
      document.body.appendChild(toolbar);
    }
    return toolbar;
  }

  /**
   * Toggle the comments sidebar
   */
  private toggleCommentsSidebar(): void {
    const existingSidebar = document.querySelector('.review-comments-sidebar');
    if (existingSidebar) {
      existingSidebar.remove();
    } else {
      this.showCommentsSidebar();
    }
  }

  /**
   * Show the comments sidebar with all comments from all sections
   */
  private showCommentsSidebar(): void {
    const sidebar = document.createElement('div');
    sidebar.className = 'review-comments-sidebar';
    sidebar.innerHTML = `
      <div class="review-comments-sidebar-header">
        <h3>Comments</h3>
        <button class="review-btn review-btn-secondary review-btn-sm" data-action="close">✕</button>
      </div>
      <div class="review-comments-sidebar-body"></div>
    `;

    sidebar
      .querySelector('[data-action="close"]')
      ?.addEventListener('click', () => {
        sidebar.classList.remove('review-sidebar-open');
        setTimeout(() => sidebar.remove(), 300); // Wait for animation
      });

    // Populate comments
    this.populateCommentsSidebar(sidebar);

    document.body.appendChild(sidebar);

    // Trigger slide-in animation
    requestAnimationFrame(() => {
      sidebar.classList.add('review-sidebar-open');
    });
  }

  /**
   * Populate the comments sidebar with comments from all sections
   */
  private populateCommentsSidebar(sidebar: HTMLElement): void {
    const body = sidebar.querySelector('.review-comments-sidebar-body');
    if (!body) return;

    const currentState = this.config.changes.getCurrentState();
    let hasComments = false;

    currentState.forEach((element) => {
      // Parse CriticMarkup from element content
      const matches = this.config.comments.parse(element.content);
      const comments = matches.filter(
        (m) => m.type === 'comment' || m.type === 'highlight'
      );

      if (comments.length > 0) {
        hasComments = true;

        // Create section for this element
        const section = document.createElement('div');
        section.className = 'review-comments-section';
        section.innerHTML = `
          <div class="review-comments-section-header">
            <strong>${element.metadata.type}</strong>
            <span class="review-comments-count">${comments.length} comment${comments.length > 1 ? 's' : ''}</span>
          </div>
          <div class="review-comments-list"></div>
        `;

        const list = section.querySelector('.review-comments-list');
        if (list) {
          comments.forEach((match, index) => {
            const commentItem = this.createCommentItem(
              match,
              element.id,
              index
            );
            list.appendChild(commentItem);
          });
        }

        body.appendChild(section);
      }
    });

    if (!hasComments) {
      body.innerHTML = `
        <div class="review-comments-empty">
          <p>No comments yet.</p>
          <p>Select text in any section to add a comment.</p>
        </div>
      `;
    }
  }

  /**
   * Create a comment item for the sidebar
   */
  private createCommentItem(
    match: ReturnType<CommentsModule['parse']>[0],
    elementId: string,
    _index: number
  ): HTMLElement {
    const item = document.createElement('div');
    item.className = 'review-comment-item';

    const commentText =
      match.type === 'highlight' && match.comment
        ? match.comment
        : match.content;

    const highlightedText =
      match.type === 'highlight'
        ? `<div class="review-comment-highlight">"${match.content.substring(0, 50)}${match.content.length > 50 ? '...' : ''}"</div>`
        : '';

    item.innerHTML = `
      ${highlightedText}
      <div class="review-comment-text">${this.escapeHtml(commentText)}</div>
      <div class="review-comment-actions">
        <button class="review-btn review-btn-sm" data-action="goto" title="Go to comment">
          📍 Go to
        </button>
        <button class="review-btn review-btn-sm" data-action="resolve" title="Resolve comment">
          ✓ Resolve
        </button>
      </div>
    `;

    item
      .querySelector('[data-action="goto"]')
      ?.addEventListener('click', () => {
        this.scrollToElement(elementId);
      });

    item
      .querySelector('[data-action="resolve"]')
      ?.addEventListener('click', () => {
        this.resolveComment(elementId, match);
        this.refresh();
        // Refresh sidebar
        const sidebar = document.querySelector('.review-comments-sidebar');
        if (sidebar) {
          sidebar.remove();
          this.showCommentsSidebar();
        }
      });

    return item;
  }

  /**
   * Scroll to and highlight an element
   */
  private scrollToElement(elementId: string): void {
    const element = document.querySelector(`[data-review-id="${elementId}"]`);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth', block: 'center' });
      element.classList.add('review-highlight-flash');
      setTimeout(() => {
        element.classList.remove('review-highlight-flash');
      }, 2000);
    }
  }

  /**
   * Resolve a comment by removing it from the content
   */
  private resolveComment(
    elementId: string,
    match: ReturnType<CommentsModule['parse']>[0]
  ): void {
    try {
      const currentContent = this.config.changes.getElementContent(elementId);
      const newContent = this.config.comments.accept(currentContent, match);
      this.config.changes.edit(elementId, newContent);
      this.showNotification('Comment resolved', 'success');
    } catch (error) {
      console.error('[QuartoReview] Failed to resolve comment:', error);
      this.showNotification('Failed to resolve comment', 'error');
    }
  }

  /**
   * Escape HTML for safe display
   */
  private escapeHtml(text: string): string {
    const div = document.createElement('div');
    div.textContent = text;
    return div.innerHTML;
  }

  public showNotification(
    message: string,
    type: 'info' | 'success' | 'error' = 'info'
  ): void {
    const notification = document.createElement('div');
    notification.className = `review-notification review-notification-${type}`;
    notification.textContent = message;
    document.body.appendChild(notification);
    setTimeout(() => {
      notification.classList.add('review-notification-show');
    }, 10);
    setTimeout(() => {
      notification.classList.remove('review-notification-show');
      setTimeout(() => notification.remove(), 300);
    }, 3000);
  }

  public showLoading(message = 'Loading...'): HTMLElement {
    const loading = document.createElement('div');
    loading.className = 'review-loading';
    loading.innerHTML = `
      <div class="review-loading-content">
        <div class="review-loading-spinner"></div>
        <p>${message}</p>
      </div>
    `;
    document.body.appendChild(loading);
    return loading;
  }

  public hideLoading(loading: HTMLElement): void {
    loading.remove();
  }

  public destroy(): void {
    this.closeEditor();
    const toolbar = document.querySelector('.review-toolbar');
    toolbar?.remove();
    const editableElements = document.querySelectorAll('.review-editable');
    editableElements.forEach((elem) => {
      const clone = elem.cloneNode(true);
      elem.parentNode?.replaceChild(clone, elem);
    });
  }

  /**
   * Handle text selection for adding comments
   */
  private handleTextSelection(_e: Event, element: globalThis.Element): void {
    const selection = window.getSelection();
    if (
      !selection ||
      selection.isCollapsed ||
      selection.toString().trim() === ''
    ) {
      // No text selected, remove any existing popup
      const existingPopup = document.querySelector('.review-comment-popup');
      existingPopup?.remove();
      return;
    }

    const selectedText = selection.toString().trim();
    const elementId = element.getAttribute('data-review-id');
    if (!elementId) return;

    // Get selection position for popup placement
    const range = selection.getRangeAt(0);
    const rect = range.getBoundingClientRect();

    // Show comment popup
    this.showCommentPopup(selectedText, elementId, rect);
  }

  /**
   * Show popup for adding a comment to selected text
   */
  private showCommentPopup(
    selectedText: string,
    elementId: string,
    rect: DOMRect
  ): void {
    // Remove existing popup if any
    const existingPopup = document.querySelector('.review-comment-popup');
    existingPopup?.remove();

    const popup = document.createElement('div');
    popup.className = 'review-comment-popup';
    popup.style.position = 'fixed';
    popup.style.top = `${rect.bottom + 5}px`;
    popup.style.left = `${rect.left}px`;
    popup.innerHTML = `
      <div class="review-comment-popup-content">
        <button class="review-btn review-btn-primary review-btn-sm" data-action="add-comment">
          💬 Add Comment
        </button>
        <button class="review-btn review-btn-secondary review-btn-sm" data-action="highlight-only">
          ✨ Highlight
        </button>
      </div>
    `;

    popup
      .querySelector('[data-action="add-comment"]')
      ?.addEventListener('click', () => {
        this.showCommentDialog(selectedText, elementId);
        popup.remove();
      });

    popup
      .querySelector('[data-action="highlight-only"]')
      ?.addEventListener('click', () => {
        this.addHighlight(selectedText, elementId);
        popup.remove();
      });

    document.body.appendChild(popup);
  }

  /**
   * Show dialog for entering comment text
   */
  private showCommentDialog(selectedText: string, elementId: string): void {
    const dialog = document.createElement('div');
    dialog.className = 'review-comment-dialog';
    dialog.innerHTML = `
      <div class="review-comment-dialog-overlay"></div>
      <div class="review-comment-dialog-content">
        <h3>Add Comment</h3>
        <p><strong>Selected text:</strong> "${selectedText.substring(0, 50)}${selectedText.length > 50 ? '...' : ''}"</p>
        <textarea
          class="review-comment-input"
          placeholder="Enter your comment..."
          rows="4"
        ></textarea>
        <div class="review-comment-dialog-actions">
          <button class="review-btn review-btn-secondary" data-action="cancel">Cancel</button>
          <button class="review-btn review-btn-primary" data-action="save">Add Comment</button>
        </div>
      </div>
    `;

    const textarea = dialog.querySelector('textarea') as HTMLTextAreaElement;
    const saveBtn = dialog.querySelector('[data-action="save"]');
    const cancelBtn = dialog.querySelector('[data-action="cancel"]');

    saveBtn?.addEventListener('click', () => {
      const comment = textarea.value.trim();
      if (comment) {
        this.addHighlight(selectedText, elementId, comment);
      }
      dialog.remove();
    });

    cancelBtn?.addEventListener('click', () => {
      dialog.remove();
    });

    dialog
      .querySelector('.review-comment-dialog-overlay')
      ?.addEventListener('click', () => {
        dialog.remove();
      });

    document.body.appendChild(dialog);
    textarea.focus();
  }

  /**
   * Add CriticMarkup highlight (with optional comment) to element
   */
  private addHighlight(
    selectedText: string,
    elementId: string,
    comment?: string
  ): void {
    try {
      // Get current content
      const currentContent = this.config.changes.getElementContent(elementId);

      // Find the selected text in the content
      const index = currentContent.indexOf(selectedText);
      if (index === -1) {
        this.showNotification(
          'Could not find selected text in markdown source',
          'error'
        );
        return;
      }

      // Create CriticMarkup syntax
      const criticMarkup = this.config.comments.createHighlight(
        selectedText,
        comment
      );

      // Replace selected text with CriticMarkup
      const newContent =
        currentContent.substring(0, index) +
        criticMarkup +
        currentContent.substring(index + selectedText.length);

      // Save the change
      this.config.changes.edit(elementId, newContent);
      this.refresh();

      // Clear selection
      window.getSelection()?.removeAllRanges();

      this.showNotification(
        comment
          ? 'Comment added successfully'
          : 'Text highlighted successfully',
        'success'
      );
    } catch (error) {
      console.error('[QuartoReview] Failed to add highlight:', error);
      this.showNotification('Failed to add highlight', 'error');
    }
  }
}

export default UIModule;
