/**
 * Git Module
 * Git operations using isomorphic-git with provider-specific extensions
 * Note: Git operations are currently disabled in browser environments
 * TODO: Implement browser-compatible git using LightningFS
 */

import type { GitProvider } from '@/types';

export interface GitConfig {
  dir: string;
  author?: {
    name: string;
    email: string;
  };
  provider?: GitProvider;
}

export interface CommitOptions {
  message: string;
  files?: string[];
}

export interface PushOptions {
  remote?: string;
  branch?: string;
  force?: boolean;
}

export interface PullOptions {
  remote?: string;
  branch?: string;
}

const GIT_NOT_SUPPORTED =
  'Git operations are not yet supported in the browser. This feature is coming soon with LightningFS support.';

export class GitModule {
  private config: GitConfig;

  constructor(providerType?: GitProvider['type']) {
    this.config = {
      dir: '.',
      author: {
        name: 'Quarto Review',
        email: 'review@quarto.org',
      },
      provider: providerType ? { type: providerType } : undefined,
    };
    console.warn(
      '[QuartoReview] Git module initialized in browser mode - git operations disabled'
    );
  }

  public setDirectory(dir: string): void {
    this.config.dir = dir;
  }

  public setAuthor(name: string, email: string): void {
    this.config.author = { name, email };
  }

  public async init(): Promise<void> {
    console.warn(GIT_NOT_SUPPORTED);
  }

  public async isRepo(): Promise<boolean> {
    return false;
  }

  public async getCurrentBranch(): Promise<string> {
    return 'main';
  }

  public async createBranch(
    _branchName: string,
    _checkout = true
  ): Promise<void> {
    console.warn(GIT_NOT_SUPPORTED);
  }

  public async checkout(_ref: string): Promise<void> {
    console.warn(GIT_NOT_SUPPORTED);
  }

  public async add(_filepath: string): Promise<void> {
    console.warn(GIT_NOT_SUPPORTED);
  }

  public async addAll(): Promise<void> {
    console.warn(GIT_NOT_SUPPORTED);
  }

  public async commit(_options: CommitOptions): Promise<string> {
    console.warn(GIT_NOT_SUPPORTED);
    return '';
  }

  public async status(): Promise<{
    modified: string[];
    added: string[];
    deleted: string[];
  }> {
    return {
      modified: [],
      added: [],
      deleted: [],
    };
  }

  public async log(_depth = 10): Promise<ReadCommitResult[]> {
    return [];
  }

  public async push(_options: PushOptions = {}): Promise<void> {
    console.warn(GIT_NOT_SUPPORTED);
  }

  public async pull(_options: PullOptions = {}): Promise<void> {
    console.warn(GIT_NOT_SUPPORTED);
  }

  public async clone(_url: string, _dir?: string): Promise<void> {
    console.warn(GIT_NOT_SUPPORTED);
  }

  public async save(
    _content: string,
    _message: string,
    _filepath = 'document.qmd'
  ): Promise<string> {
    console.warn(GIT_NOT_SUPPORTED);
    return '';
  }

  public async readFile(_filepath: string): Promise<string> {
    console.warn(GIT_NOT_SUPPORTED);
    return '';
  }

  public async diff(_commitA: string, _commitB: string): Promise<string[]> {
    return [];
  }
}

// Type for commit result
interface ReadCommitResult {
  oid: string;
  commit: {
    message: string;
    tree: string;
    parent: string[];
    author: {
      name: string;
      email: string;
      timestamp: number;
      timezoneOffset: number;
    };
    committer: {
      name: string;
      email: string;
      timestamp: number;
      timezoneOffset: number;
    };
  };
  payload: string;
}

export default GitModule;
