import {
  $mark,
  $inputRule,
  $prose,
  $useKeymap,
  $remark,
} from '@milkdown/utils';
import { Plugin, PluginKey } from '@milkdown/prose/state';
import type { MarkSchema } from '@milkdown/transformer';
import { toggleMark } from '@milkdown/prose/commands';
import { schemaCtx } from '@milkdown/core';
import { remarkCriticMarkupMilkdown } from './remark-criticmarkup-milkdown';

// ---- MARK HELPERS ---- //

function createCriticMark(name: string, className: string): MarkSchema {
  return {
    // Prevent invalid mark nesting in lists or other blocks
    excludes: '',

    parseDOM: [
      { tag: `span[data-critic="${name}"]` },
      ...(name === 'addition' ? [{ tag: 'ins' }] : []),
      ...(name === 'deletion' ? [{ tag: 'del' }] : []),
      ...(name === 'highlight' ? [{ tag: 'mark' }] : []),
    ],

    toDOM: (): [string, Record<string, string>, 0] => {
      if (name === 'addition')
        return ['ins', { 'data-critic': name, class: className }, 0];
      if (name === 'deletion')
        return ['del', { 'data-critic': name, class: className }, 0];
      if (name === 'highlight')
        return ['mark', { 'data-critic': name, class: className }, 0];
      return ['span', { 'data-critic': name, class: className }, 0];
    },

    parseMarkdown: {
      match: (node: any) =>
        node.type === 'criticMarkup' && node.markup === name,
      runner: (state: any, node: any, markType: any) => {
        state.openMark(markType);
        // Create completely new text nodes without ANY references to original nodes
        // This prevents "cyclic object value" errors from parent references added by unist-util-visit
        const textNodes = (node.children || [])
          .filter((child: any) => child.type === 'text')
          .map((child: any) => ({
            type: 'text',
            value: String(child.value || '')
          }));
        state.next(textNodes);
        state.closeMark(markType);
      },
    },

    toMarkdown: {
      match: (mark: any) => mark.type.name === name,
      runner: (state: any) => {
        const map: Record<string, [string, string]> = {
          addition: ['{++', '++}'],
          deletion: ['{--', '--}'],
          highlight: ['{==', '==}'],
          comment: ['{>>', '<<}'],
          substitution: ['{~~', '~~}'],
        };

        const [open, close] = map[name] ?? ['', ''];

        // 🧩 Fix for list corruption:
        // If currently inside a list item, force text to be serialized inside paragraph context
        if (
          state.stack.length > 0 &&
          state.stack[state.stack.length - 1].type === 'list_item'
        ) {
          if (!state.inParagraph) {
            state.openNode('paragraph');
            state.write(open);
            state.closeNode();
            state.openNode('paragraph');
          } else {
            state.write(open);
          }
        } else {
          state.write(open);
        }

        // Continue writing mark contents
        state.next();
        state.write(close);

        // Close paragraph context if it was opened artificially
        if (
          state.inParagraph &&
          state.stack.length > 0 &&
          state.stack[state.stack.length - 1].type === 'list_item'
        ) {
          state.closeNode();
        }
      },
    },
  };
}

// ---- MARK DEFINITIONS ---- //

export const criticAddition = $mark('criticAddition', () =>
  createCriticMark('addition', 'critic-addition')
);
export const criticDeletion = $mark('criticDeletion', () =>
  createCriticMark('deletion', 'critic-deletion')
);
export const criticHighlight = $mark('criticHighlight', () =>
  createCriticMark('highlight', 'critic-highlight')
);
export const criticComment = $mark('criticComment', () =>
  createCriticMark('comment', 'critic-comment')
);
export const criticSubstitution = $mark('criticSubstitution', () =>
  createCriticMark('substitution', 'critic-substitution')
);

// ---- INPUT RULES ---- //

function makeCriticRule(regex: RegExp) {
  return $inputRule(() => ({
    match: regex,
    inCode: false, // <--- required by InputRule
    inCodeMark: false, // <--- required by InputRule
    handler: ({
      state,
      range,
      match,
    }: {
      state: any;
      range: any;
      match: any;
    }) => {
      const tr = state.tr.insertText(match[1], range.from, range.to);
      return tr;
    },
  }));
}

// Example (optional, placeholder)
export const criticAdditionRule = makeCriticRule(/\{([+]{2})([^}]+)\1}/);

// ---- KEYMAP ---- //

export const criticKeymap = $useKeymap(
  'criticKeymap',
  (ctx: { get: (arg0: any) => any }) => {
    const schema = ctx.get(schemaCtx); // ✅ get schema correctly

    return {
      'Mod-Shift-A': toggleMark(schema.marks.criticAddition),
      'Mod-Shift-D': toggleMark(schema.marks.criticDeletion),
      'Mod-Shift-H': toggleMark(schema.marks.criticHighlight),
      'Mod-Shift-C': toggleMark(schema.marks.criticComment),
      'Mod-Shift-S': toggleMark(schema.marks.criticSubstitution),
    };
  }
);

// ---- OPTIONAL: DECORATION PLUGIN (empty placeholder, safe stub) ---- //

export const criticDecorationPlugin = $prose(() => {
  return new Plugin({
    key: new PluginKey('criticDecoration'),
    props: {
      decorations() {
        return null; // no decorations yet
      },
    },
  });
});

export const criticMarkupPlugin = $prose(() => {
  return new Plugin({
    key: new PluginKey('criticMarkup'),
  });
});

// ---- REMARK PLUGIN ---- //

export const criticMarkupRemarkPlugin = $remark(
  'criticMarkupRemark',
  () => remarkCriticMarkupMilkdown
);
